<div class="custom-container warning"><p class="custom-container-title">Repetition</p>
<p>To tell the <code>ConstraintLayout</code> where a child should be positioned horizontally, we can add one of the following attributes (known as a <em>constraint</em>) to the child:</p>
<ul>
<li><code>app:layout_constraintLeft_toLeftOf</code></li>
<li><code>app:layout_constraintLeft_toRightOf</code></li>
<li><code>app:layout_constraintRight_toLeftOf</code></li>
<li><code>app:layout_constraintRight_toRightOf</code></li>
</ul>
<p>The value can either be <code>parent</code>, or the id of one of the other child <code>View</code>s in the <code>ConstraintLayout</code>.</p>
<p>Similarly, to tell the <code>ConstraintLayout</code> where a child should be positioned vertically, we can add one of the following attributes to the child:</p>
<ul>
<li><code>app:layout_constraintTop_toTopOf</code></li>
<li><code>app:layout_constraintTop_toBottomOf</code></li>
<li><code>app:layout_constraintBottom_toTopOf</code></li>
<li><code>app:layout_constraintBottom_toBottomOf</code></li>
</ul>
<p>The value can either be <code>parent</code>, or the id of one of the other child <code>View</code>s in the <code>ConstraintLayout</code>.</p>
</div>
<p>Instead of giving a child it's size through the <code>layout_width</code> and <code>layout_height</code> attributes, you can specify two horizontal (or two vertical) constraints, and the child can get its width (or height) from the constraints instead. When a child has two horizontal constraints (or two vertical constraints) and you want the child to get its size through the constraints you should set the <code>layout_width</code> (or <code>layout_height</code>) to <code>0dp</code>.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how to position two <code>View</code>s next to each other horizontally:</p>
<ul>
<li>The first one is as small as possible</li>
<li>The second one should occupy the rest of the remaining space</li>
</ul>
